.nav-pills {
	.nav-item {
		a {
			color: $body-color;
			font-size: 16px;
			font-weight: 500;
			padding: 0.938rem 1.875rem;
			margin-right: 0px;
		}
		&:last-child {
			a {
				margin-right: 0;
			}
		}
	}
	.nav-link.active {
		color: $primary;
		background: $primary-light;
	}
	.show {
		>.nav-link {
			color: $primary;
			background: $primary-light;
		}
	}
	.nav-link {
		&:hover {
			color: $primary;
		}
	}
}
.nav-tabs {
	border-bottom: 2px solid $primary-light;
	margin-bottom: 15px;
	.nav-item {
		margin-bottom: -2px;
		a {
			color: $body-color;
			border: none;
			border-bottom: 2px solid transparent;
		}
	}
	.nav-item.show {
		.nav-link {
			border-bottom: 2px solid $primary;
			color: $primary;
		}
	}
	.nav-link.active {
		border-bottom: 2px solid $primary;
		color: $primary;
	}
}
.nav-pills {
	&.tab-pills {
		.nav-item {
			a {
				color: $body-color;
			}
		}
		.nav-link.active {
			color: $primary;
			background: $white;
		}
		.show {
			>.nav-link {
				color: $primary;
				background: $white;
			}
		}
		.nav-link {
			@if $enable-rounded == true {
				@include radius-mixin($border-radius-top);
			}
			&:hover {
				color: $primary;
			}
		}
	}
}
.body-tab-pills {
	background: $white;
	.nav-item {
		background: $white;

		&:hover {
			.nav-link {
				background: $body-bg;
			}
		}
		.nav-link {
			&.active {
				background: $body-bg;
			}
		}
	}
}
