.iq-icon-box-2 {
	height: 50px;
    width: 50px;
    line-height: 45px;
	text-align: center;
	margin-right: 20px;
	font-size: 26px;
	@if $enable-rounded == true {
		@include radius-mixin($border-radius);
	}
	&.icon-text {
		line-height: 52px;
	}
}
.iq-icon-box-3 {
	height: 100px;
    width: 100px;
    line-height: 95px;
	text-align: center;
	background: $body-bg;
	color: $primary;
	font-size: 26px;
	@if $enable-rounded == true {
		@include radius-mixin($border-radius);
	}	
	@if $enable-transitions == true {
		@include transition-mixin(all 0.3s ease-in-out);
	}	
}
.task-content {
	.card {
		&:hover {
			.iq-icon-box-3 {
				background: $primary;
				color: $white !important;
			}
		}
	}
}
.iq-search-bar {
	&.card-search {
		.searchbox {
			width: 100%;
		}
		.search-link {
			left: 15px;
			right: auto;
			background: none;
			color: $body-text;
			border-radius: 0;
		}
		.search-input {
			padding: 5px 15px 5px 55px;
		}
	}
}
.task-btn {
	margin-bottom: 30px;
	a {
		font-size: 16px;
		font-weight: 600;
		background: $body-bg;
		color: $primary;
		margin-right: 30px;

		&:last-child {
			margin-right: 0;
		}
	}
}

@media (min-width:992px){
	.task-ip {
		li {
			position: relative;
			&:before {
				content: "";
				border-right: 1px solid $border-color;
				height: 100%;
				width: auto;
				position: absolute;
				right: 0px;
				top: 0;
			}
			&:last-child {
				&:before {
					display: none;
				}
			}
		}	
	}
}
@media (max-width: 1199px) {
	.task-ip {
		li {
			&:before {
				right: 10px;
			}
		}
	}
}
.kubernets-card {
	@if $enable-transitions == true {
		@include transition-mixin(all 0.3s ease-in-out);
	}
	.iq-icon-box-3 {
		background: $white;
	}
	&:hover {
		background: $white;

		.iq-icon-box-3 {
			background: $primary;
			color: $white;
		}
	}
}
.create-droplet {
	.dropdown {
		.dropdown-toggle {
			color: $primary;
			font-weight: 500;
			&:hover {
				background: $primary;
				color: $white;
			}
			&:after {
				display: none;
			}
			i {
				vertical-align: middle;
			}
		}
	}
}
.btn-group-droplets {
	position: absolute;
    right: 15px;
    bottom: 30px;
	.button-icon {
		font-size: 20px;
		line-height: 22px;
	}
}
.select-toggle {
	.dropdown-toggle {
		i {
			vertical-align: bottom;
		}
		&:after {
			display: none;
		}
	}
}
@media (max-width: 991px) {
	.task-ip {
		li {
			div {
				justify-content: left !important;
			}
		}
	}	
	.btn-group-droplets {
		bottom: 20px;
	}
}
@media (max-width: 490px) {
	.responsive-icon {
		flex: 1;
		text-align: center;
	}
}
@media (max-width: 390px) {
	.icon-responsive {
		margin-bottom: 10px;
	}
}
.profile-image{
	max-height: 90vh;
	img {
		transform: translateY(-40px);
		object-fit: cover;
	}
    overflow: hidden;
}