.table {
    border: 1px solid $border-color;
    border-radius: $border-radius;
    overflow: hidden;
    color: $titletext;
    margin-bottom: 0;
    &.tree {
        td {
            padding: 15px 12px !important;
        }
    }
    &.table-dark {
        th {
            border-color: $body-title;
            color: $white;
        }
        td {
            border-color: $body-title;
            color: $white;
        }
    }
    span, p{
        color: $bodytext;
    }
    .ligth {
        white-space: nowrap;
        th {
            text-transform: capitalize;
            font-size: 19px;
            line-height: 22px;
            padding-top: 30px;
            padding-bottom: 30px;
            border-bottom: 1px solid $border-color;
            background: transparent;
            color: $body-title;
            font-weight: 500 !important;           
        }
        &.ligth-data {
            th {
                &:last-child {
                    background-image: none;
                    text-align: center;
                }
            }
        }
    }
    .ligth-body {
        tr {    
            td { 
                &:last-child {
                    text-align: center;
                }
            }  
        }
        .list-action {
            justify-content: center;
        }
    }
    td {
        padding: 30px 12px !important;
        border: 0px;
        border-bottom: 1px solid $border-color;
        color: $body-text;

        
    }
    label {
        color: $body-title;
    }
    .dropdown {
        .dropdown-toggle {
            &:after {
                display: none;
            }
        }
    }
    &.tbl-networking-info {
        td {
            color: $dark;
        }
    }
}
.table-responsive {    
    &.tbl-server-info {
        padding: 0 20px;
        margin-bottom: 30px;
    }
    padding: 0px ;
    background: $body-bg;
}
.btn {
    &.add-list {
        padding: 0.5rem 1rem;
    }
}
@media (max-width: 767px) {
    .table-responsive { 
        padding: 20px;
    }
    .table-responsive {
        &.tbl-server-info {
            padding: 15px;
            margin-bottom: 20px;
        }
    }
    .table {
        td {
            padding-top: 20px !important;
            padding-bottom: 20px !important;
        }
    }
    .tbl-networking-info {
        margin: 20px 0 !important;
    }
}