.custom-checkbox.checkbox-icon {
	label {
		i {
			position: absolute;
			top: 6px;
			left: -21px;
			font-size: 10px;
			@include transform-mixin(scale(0));
			@if $enable-transitions == true {
		@include transition-mixin(all 0.3s ease-in-out);
	}
		}
	}
	input {
		&:checked~label {
			i {
				@include transform-mixin(scale(1));	
				color: $white;
			}
			&::after {
				display: none;
			}
		}
	}
}

/* Custom Check Box */

.checkbox {
	&.custom-checkbox {
		width: auto;
		position: relative;
		display: block;
		input[type="checkbox"] {
			width: auto;
			opacity: 0.00000001;
			position: absolute;
			left: 0;
			margin-left: -20px;
		}
		label {
			position: relative;
			min-height: auto;
			display: block;
			padding-left: 40px;
			margin-bottom: 0;
			font-weight: normal;
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				margin: 3px 0 0;
				width: 20px;
				height: 20px;
				transition: transform 0.28s ease;
				border-radius: 3px;
				border: 1px solid $primary;
			}
			&:after {
				content: '';
				display: block;
				width: 10px;
				height: 5px;
				border-bottom: 2px solid $primary;
				border-left: 2px solid $primary;
				-webkit-transform: rotate(-45deg) scale(0);
				transform: rotate(-45deg) scale(0);
				transition: transform ease 0.25s;
				will-change: transform;
				position: absolute;
				top: 9px;
				left: 6px;
			}
			span {
				position: absolute;
				top: 50%;
				-webkit-transform: translateY(-50%);
				transform: translateY(-50%);
			}
		}
		input[type="checkbox"]:checked {
			& ~ label {
				&::before {
					color: $primary;
				}
				&::after {
					-webkit-transform: rotate(-45deg) scale(1);
					transform: rotate(-45deg) scale(1);
				}
			}
		}
		input[type="checkbox"]:focus {
			& + label {
				&::before {
					outline: 0;
				}
			}
		}
	}
}


/*checkboxes toggle*/


.checkbox-toggle {
	input {
		display: none;
	}
	label {
		outline: 0;
		display: block;
		width: 45px;
		height: 17px;
		background:$light;
		position: relative;
		cursor: pointer;
		border-radius: 2em;
		padding: 2px;
		transition: all 0.4s ease;
		&:after {
			position: relative;
			display: block;
			content: "";
			width: 24px;
			height: 24px;
			border-radius: 50%;
			background:$body-text;
			transition: all 0.2s ease;
			border: 1px solid;
			border-color: $body-text;
			box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
			left: -2px;
			top: -5px;
		}
	}
	input:checked + label {
		background: $light;
		&:after {
			left: 50%;
		}
	}
	
}

@each $color, $value in $theme-colors {
	@include switch-variant('.custom-switch-#{$color}',$value,lighten($value,50%));
}

