.content-page {
	overflow: hidden;
	margin-left: 300px;
	padding: 105px 15px 0px;
	min-height: 100vh;
	@if $enable-transitions == true {
		@include transition-mixin(all 0.3s ease-in-out);
	}
}

@media (min-width: 1300px){
	body.sidebar-main {
		.content-page {
			margin-left: 70px;
		}
		.iq-menu-bt-sidebar{
			display: block;
			margin-left: auto;
			margin-right: auto;
			padding-left: 0;
		}
	}
}

@media(max-width: 1299px){
	.content-page{
		margin-left: 70px;
		padding: 105px 15px 0;
	}
}

@media (max-width: 991px){
	.content-page {
		padding: 85px 0 0;
	}
}
@media (max-width: 479px){
	.welcome-content {
		display: block !important;
		text-align: center;
		.breadcrumb {
			justify-content: center;
		}
	}
}