// Two sidebar design

// sidebar
.sidebar-1.sidebar-main{
	.iq-sidebar.iq-sidebar-hover{
			left: 0;
			width: 230px;
		}
		.iq-sidebar{
			&:hover{
				left: 0;
				width: 230px;
			}
			.iq-sidebar-menu {
				.iq-menu {
					li {
						a {
							span {
								@include opacity-mixin(1);
								display: block;
							}
							.iq-arrow-right {
								display: block;
							}
							.iq-arrow-right.arrow-hover {
								display: none;
							}
							i.iq-arrow-left {
									margin: 0 10px 0 0;
									text-align: left;
								}
						}
						a[aria-expanded=true] {
							.iq-arrow-right.arrow-active {
								display: none;
							}
							.iq-arrow-right.arrow-hover {
								display: block;
							}
						}
						li {
							
							a {
								i {
									margin: 0 10px 0 0;
									text-align: left;
								}
							}
						}
					}
				}
			}
			.iq-submenu {
				li {
					a {
						font-size: 14px;
					}
				}
			}
		}
	.two-sidebar {
		.main-circle{
			display: none;
			@if $enable-transitions == true {
				@include transition-mixin(all 0.3s ease-in-out);
			}
		}
		.main-circle.close-btn {
			display: block;
			@if $enable-transitions == true {
				@include transition-mixin(all 0.3s ease-in-out);
			}
		}
	}
}
.iq-sidebar-menu {
	.iq-menu {
		li {
			
			a {
				font-size: 16px;
				white-space: nowrap;
				display: block;
				color: $light;
				position: relative;
				padding: 15px 0px;
				line-height: 18px;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
				-ms-flex-align: center;
				align-items: center;
				overflow: hidden;
				text-transform: capitalize;
				@if $enable-rounded == true {
					@include radius-mixin($border-radius);
				}
				@include opacity-mixin(1);

				i {
					vertical-align: middle;
					width: 20px;
					display: inline-block;
					margin-right: 8px;
					font-size: 16px;
				}
				span {
					white-space: nowrap;
					padding: 0;
					display: inline-block;
					@if $enable-transitions == true {
						@include transition-mixin(none);
					}
				}
				.iq-arrow-right {
					margin-right: 0px;
					margin-left: auto;	
					font-size: 15px;
					width: 15px;
					@if $enable-transitions == true {
						@include transition-mixin(all 0.6s ease-in-out);
					}
				}
				.badge {
					position: absolute;
					right: 15px;
					@if $enable-transitions == true {
						@include transition-mixin(all 0.6s ease-in-out);
					}
				}
			}
			a[aria-expanded="true"] {
				.iq-arrow-right {
					@if $enable-transitions == true {
						@include transition-mixin(all 0.6s ease-in-out);
					}
				}
			}	
			a {
				.iq-arrow-right.arrow-hover {
					@include opacity-mixin(0);
					display: none;
				}
			}
			a[aria-expanded="true"] {
				.iq-arrow-right.arrow-active {
					@include opacity-mixin(0);
					display: none;
				}
				.iq-arrow-right.arrow-hover {
					@include opacity-mixin(1);
					display: block;
				}
			}
		}
	}
}
.two-sidebar {
	.iq-top-navbar {
		.iq-navbar-custom {
			.iq-menu-bt {
				.wrapper-menu {
					display: none;
				}
			}
		}
	}
	.main-circle.close-btn {
		display: none;
	}
	.iq-sidebar-small {
		z-index: 1030;
		position: fixed;
		width: 70px;
		height: 100vh;
		top: 0;
		left: 0;
		padding: 15px 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		text-align: center;
		background-color:$white;
		border-right: 1px solid $border-color;
		.iq-sidebar-small-logo {
			padding: 0 5px;
			margin-bottom: 20px;
			img {
				height: 40px;
			}
		}
	}
	.iq-menu-bt {
		width: 40px;
		margin: 0 auto;
		line-height: 17px;
		font-size: 20px;
		background: $white;
		@if $enable-rounded == true {
			@include radius-mixin($border-radius);
		}
	}
	.iq-sidebar {
		.iq-navbar-logo {
			.iq-menu-bt {
				.wrapper-menu {
					display: none;
				}
			}
		}
		margin-left: 70px;
		top: 0px;
		z-index: 999;
		position: fixed;
		min-height: 100vh;
		background: $white;
		border: 0;
	}
	.content-page {
		margin-left: 320px;
	}
	.iq-timeline {
		li {
			.timeline-dots {
				height: 10px;
				width: 10px;
				left: -44px;
			}
		}
	}
}
.sidebar-menu-icon {
	display: flex;
	flex-direction: column;
	padding: 0 15px;
	>a {
		font-size: 18px;
		display: block;
		padding: 5px 0;
		color: $dark;
	}
	.iq-menu-bt{
		background: transparent;
		.wrapper-menu{
			font-size: 25px;
			color: $dark;
		}
	}
	
}
.menu-open {
	@include opacity-mixin(0);
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	text-align: center;
	margin: 0 auto;
	border-radius: 5px;
}
.wrapper-menu.open {
	.menu-close {
		@include opacity-mixin(0);
	}
	.menu-open {
		@include opacity-mixin(1);
		top: 12px;
	}
}
body.two-sidebar.sidebar-main {
	.iq-sidebar {
		width: 0px;
	}
}
.two-sidebar.sidebar-main {
	.content-page {
		margin-left: 80px;
	}
}
.iq-page-menu-horizontal {
	.iq-timeline {
		li {
			.timeline-dots {
				height: 10px;
				width: 10px;
				left: -44px;
			}
		}
	}
}
.btn-new {
	font-size: 16px !important;

	.newbtn-icon {
		font-size: 20px;
		line-height: 24px;
		margin-right: 5px;
		margin-left: 10px;
		float: left;
	}
}
.sidebar-1 {
	.nav-pills {
		li {
			a.active {
				color: $white;
				background-color: $primary;
				&:before {
					@include opacity-mixin(1);
					@if $enable-transitions == true {
						@include transition-mixin(all 0.3s ease-in-out);
					}
				}
			}
			position: relative;
			margin-bottom: 15px;
		}
		.nav-item {
			a {
				height: 40px;
				width: 40px;
				line-height: 36px;
				padding: 0px;
				color: $body-text;
			}
		}
	}
	.iq-menu {
		padding: 0 20px;
		li {
			a {
				color: $body-text;				
				&:hover {
					color: $primary;
				}
			}
		}
	}
}
.two-sidebar {
	.iq-sidebar-menu {
		.iq-menu {
			li {
				ul{
					list-style: none;
					padding-left: 10px;
				}
			}
			li.active {
				>a {
					color: $primary;
					>i.iq-arrow-left {
						color: $primary;
					}
				}
			}
		}
	}
}
.iq-sidebar-top {
	padding: 25px 20px;
    border-bottom: 0;
	margin-bottom: 10px;
	.top-menu-icon {
		.menu-icon {
			>a {
				font-size: 26px;
				height: 42px;
				width: 42px;
				line-height: 44px;
				display: block;
				text-align: center;
				@if $enable-rounded == true {
					@include radius-mixin($border-radius);
				}
			}
		}
	}
}

// content page
.orange-background-image{
	background: #ffdcd8;
}
.sidebar-1 {
	.iq-top-navbar {
		width: calc(100% - 310px);
	}
	.content-page {
		margin-left: 310px;
	}
	.iq-sidebar {
		width: 230px;
		&:hover {
			left: 0;
		}
		&.iq-sidebar-hover {
			left: 0;
		}
	}
	
	.iq-footer {
		margin-left: 310px;
		width: calc(100vw - 315px);
	}
	.iq-top-navbar{
		.iq-menu-bt {
			display: none;
		}
	}
	
}

@media(min-width:1300px) {
	.iq-top-navbar {
		.iq-navbar-custom {
			.wrapper-menu	 {
				display: none;
			}
		}
	}
	body.sidebar-main {
		.iq-sidebar {
			width: 60px;
			.iq-sidebar-menu {
				.iq-menu {
					li {
						a {
							span {
								@include opacity-mixin(0);
								display: none;
								@if $enable-transitions == true {
									@include transition-mixin(all 0.6s ease-in-out);
								}
							}
							.iq-arrow-right {
								display: none;
							}
						}
					}					
					.iq-submenu {
						display: none;
					}
				}
			}
		}
		.iq-sidebar-logo {
			transform: translateX(-100px);
			a {
				span {
					@include opacity-mixin(0);
					display: none;
					@if $enable-transitions == true {
						@include transition-mixin(all 0.6s ease-in-out);
					}
				}
				@include opacity-mixin(0);
				display: flex;
				@if $enable-transitions == true {
					@include transition-mixin(all 0.6s ease-in-out);
				}
			}
		}
		.iq-sidebar-menu {
			.iq-menu {
				li {
					a {
						.badge {
							@include opacity-mixin(0);
							display: none;
							@if $enable-transitions == true {
								@include transition-mixin(all 0.6s ease-in-out);
							}
						}
						.iq-arrow-right {
							margin-right: 0;
							display: none;
						}
						i.iq-arrow-left {
							margin: 0 auto;
						}
					}
					li {
						a {
							i {
								margin: 0 auto;
								text-align: center;
							}
						}
					}
				}
			}
		}
		.sidebar-default {
			.iq-sidebar-menu {
				.iq-menu {
					li {
						ul {
							li {
								a {
									padding-left: 18px;
								}
							}
						}
					}
				}
			}
		}
	}
	.sidebar-main {
		.iq-sidebar {
			.iq-submenu {
				li {
					a {
						font-size: 0;
					}
				}
			}
			.sidebar-bottom{
				display: none;
			}
		}
	}
}

@media(max-width: 1299px){
	.iq-sidebar {
		display: inline-block;
		z-index: 99;
		left: -300px;
		top: 0px;
		@if $enable-transitions == true {
			@include transition-mixin(all 0.3s ease-in-out);
		}		
	}
	body.sidebar-main {
		.iq-sidebar {
			width: 260px;
			left: 0;
			z-index: 999;
		}
	}

}
