.radio {
    &.custom-radio {
        & > input[type="radio"] {
            display: none;
            & + *::before {
                content: "";
                display: inline-block;
                vertical-align: text-bottom;
                width: 20px;
                height: 20px;
                margin-right: 20px;
                border-radius: 50%;
                border: 1px solid $primary;
            }
        }
        & > input[type="radio"]:checked {
            & + * {
                color: $primary;
            }
            & + *::before {
                background: radial-gradient($primary 0%, $primary 34%, transparent 40%, transparent);
                border-color: $primary;
            }
        }
    }
}